import React, { useState } from 'react';
import Sidebar from './components/Sidebar';
import { PieChart, Pie, Cell, BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import './styles/pollviewcreator.css';
import { useNavigate } from 'react-router-dom';

const PollView = () => {
  const navigate = useNavigate();

  const data = [
    { name: ' 1', value: 170, color: '#4F46E5' },
    { name: ' 2', value: 153, color: '#10B981' },
    { name: ' 3', value: 17, color: '#FACC15' },
    { name: ' 4', value: 0, color: '#F87171' },
  ];

  const totalVotes = data.reduce((sum, item) => sum + item.value, 0);

  const [chartType, setChartType] = useState('pie');

  return (
    <div className="main-layout">
      <Sidebar />
      <div className="poll-view-content">
        <button className="back-button" onClick={() => navigate(-1)}></button>

        <div className="poll-header">
          <div className="poll-icon" />
          <h1 className="poll-title">[ ]</h1>
        </div>

        <div className="poll-stats">
          <div>: <strong>500</strong></div>
          <div>: <strong>367</strong></div>
          <div>: <strong>{totalVotes}</strong></div>
        </div>

        <div className="poll-body">
          <div className="options-list">
            {data.map((option, i) => (
              <div className="option-item" key={i}>
                <span className="option-color" style={{ backgroundColor: option.color }} />
                <span>{option.name}</span>
                <span><strong>{((option.value / totalVotes) * 100).toFixed(0)}%</strong></span>
              </div>
            ))}
          </div>

          <div className="chart-container">
            <ResponsiveContainer width="100%" height={300}>
              {chartType === 'pie' ? (
                <PieChart>
                  <Pie data={data} dataKey="value" innerRadius={60} outerRadius={100}>
                    {data.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip />
                </PieChart>
              ) : (
                <BarChart data={data}>
                  <XAxis dataKey="name" />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="value">
                    {data.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Bar>
                </BarChart>
              )}
            </ResponsiveContainer>
          </div>
        </div>

        <div className="controls">
          <div className="view-switch">
            <button onClick={() => setChartType('pie')} className={chartType === 'pie' ? 'active' : ''}> </button>
            <button onClick={() => setChartType('bar')} className={chartType === 'bar' ? 'active' : ''}> </button>
          </div>
          <button className="export-button"></button>
        </div>
      </div>
    </div>
  );
};

export default PollView;
