import React from 'react';
import './styles/profile.css';
import Sidebar from "./components/Sidebar";
import { Link } from 'react-router-dom';


const ProfilePage = () => {
  const dummyPolls = [
    { id: 1, title: '[ ]', votes: 100, open: true },
    { id: 2, title: '[ ]', votes: 85, open: false },
    { id: 3, title: '[ ]', votes: 123, open: true },
    { id: 4, title: '[ ]', votes: 77, open: false },
    { id: 5, title: '[ ]', votes: 56, open: true },
    { id: 6, title: '[ ]', votes: 200, open: false },
  ];



  return (
    <div className='main-layout'>
      <Sidebar />
      <div className="profile-content">
        <div className="profile-header">
          <img
            src="https://api.vickz.ru/get-profile-picture/11e2223f-1ed0-57ee-aeaf-ef363b30b7fb"
            alt="Avatar"
            className="profile-avatar"
          />
          <div className="profile-info">
            <h2>[NAME]</h2>
            <p> </p>
            <div className="profile-stats">
              <div>: 12</div>
              <div>: 8</div>
            </div>
          </div>
        </div>

        <div className="profile-tabs">
          <button className="tab-btn active"> </button>
          <button className="tab-btn"> </button>
        </div>

        <div className="votes-grid">
          {/*  "  " */}
        <Link to='/new-poll'>
          <div className="vote-card create-card">
            <div className="create-icon">
              <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
              </svg>
            </div>
            <div className="create-text"> </div>
          </div>
        </Link>
          {dummyPolls.map((poll) => (
            <div key={poll.id} className="vote-card">
              <div className="vote-header">
                <div className="avatar-placeholder" />
                <span className={`vote-status ${poll.open ? 'open' : 'closed'}`}>
                  {poll.open ? ': 15 ' : ''}
                </span>
              </div>
              <div className="vote-body">
                <h3>{poll.title}</h3>
                <p>: {poll.votes}</p>
                <div className="vote-results">
                  <div className="vote-option yellow"> 1  75%</div>
                  <div className="vote-option green"> 2  10%</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ProfilePage;
